import React, {useState} from 'react';
import './bootstrap.min.css';
//import Buttons from './Buttons.js';

function App() {
  const [solu, setSolu] = useState("");
  const [val1, setVal1] = useState("0");
  const [val2, setVal2] = useState("");
  const [actVal, setVal] = useState(true)
  const [op, setOp] = useState("");

  function onNumClick(val) {
    if(actVal){
      if(val1 === "0") {
        setVal1(val)
      }else{
        setVal1(val1.concat(val));
      }
    }else{
      if(val2 !== "" || val !== "0") {
        setVal2(val2.concat(val));
      }
    }
  }
  function onOpClick(oper) {
    if(actVal){
      if(val1 !== "" && val1 !== "-"){
        setOp(oper);
        setVal(false);
      }else if(oper === "-" && val1 === ""){
        setVal1(val1.concat(oper));
      }
    }else{
      if (val2 !== "" && val2 !=="-"){
        /*run equals function, then  set val1 to
        the solution, and op to the op clicked*/
        onEqualClick();
        setVal(false);
        setOp(oper);
      }else{
        if(oper === "-" && val2 === ""){
          setVal2(val2.concat(oper))
        }else{
          setOp(oper);
          setVal2("");
        }
      }
    }
  }
  function onAcClick() {
    setSolu("");
    setVal1("0");
    setVal2("");
    setOp("");
    setVal(true);
  }

  function onDeciClick() {
    if(actVal && val1.indexOf(".")===-1) {
      setVal1(val1.concat("."))
    }
    if(!actVal && val2.indexOf(".")===-1) {
      setVal2(val2.concat("."))
    }
  }

  function onEqualClick() {
    if(val2!=="") {
      let ans = 0;
      if (op === "+"){
        ans = parseFloat(val1) + parseFloat(val2)
      } else if (op === "-"){
        ans = parseFloat(val1) - parseFloat(val2)
      } else if (op === "*"){
        ans = parseFloat(val1) * parseFloat(val2)
      } else if (op === "/"){
        ans = parseFloat(val1) / parseFloat(val2)
      }
      onAcClick();
      setVal1(ans.toString());
    }
  }

  return (
    <main className="container-sm" id="app">
      <div className="row">
        <div className="col-3"/>
        <div className="col-6">
          <code id="display" className="alert alert-secondary text-right">
           {val1}{op}{val2}{solu}
          </code>
        </div>
        <div className="col-3"/>
      </div><br/>
      <div className="row">
        <div className="col-3"/>
        <div className="col-6">
          <table className="buttons">
            <tbody>
              <tr>
                <td>
                  <button className="btn btn-sm btn-secondary" id="one" onClick={() => onNumClick("1")}>1</button>
                </td><td>
                  <button className="btn btn-sm btn-secondary" id="two" onClick={() => onNumClick("2")}>2</button>
                </td><td>
                  <button className="btn btn-sm btn-secondary" id="three" onClick={() => onNumClick("3")}>3</button>
                </td><td>
                  <button className="btn btn-sm btn-secondary" id="add" onClick={() => onOpClick("+")}>+</button>
                </td>
              </tr><tr>
                <td>
                  <button className="btn btn-sm btn-secondary" id="four" onClick={() => onNumClick("4")}>4</button>
                </td><td>
                  <button className="btn btn-sm btn-secondary" id="five" onClick={() => onNumClick("5")}>5</button>
                </td><td>
                  <button className="btn btn-sm btn-secondary" id="six" onClick={() => onNumClick("6")}>6</button>
                </td><td>
                  <button className="btn btn-sm btn-secondary" id="subtract" onClick={() => onOpClick("-")}>-</button>
                </td>
              </tr><tr>
                <td>
                  <button className="btn btn-sm btn-secondary" id="seven" onClick={() => onNumClick("7")}>7</button>
                </td><td>
                  <button className="btn btn-sm btn-secondary" id="eight" onClick={() => onNumClick("8")}>8</button>
                </td><td>
                  <button className="btn btn-sm btn-secondary" id="nine" onClick={() => onNumClick("9")}>9</button>
                </td><td>
                  <button className="btn btn-sm btn-secondary" id="multiply" onClick={() => onOpClick("*")}>*</button>
                </td>
              </tr><tr>
                <td>
                <button className="btn btn-sm btn-secondary" id="clear" onClick={() => onAcClick()}>AC</button>
                </td><td>
                <button className="btn btn-sm btn-secondary" id="zero" onClick={() => onNumClick("0")}>0</button>
                </td><td>
                <button className="btn btn-sm btn-secondary" id="decimal" onClick={() => onDeciClick()}>.</button>
                </td><td>
                <button className="btn btn-sm btn-secondary" id="divide" onClick={() => onOpClick("/")}>/</button>
                </td>
              </tr>
              <tr>
                <td><button className="btn btn-sm btn-secondary" id="equals" onClick={() => onEqualClick()}>=</button></td>
              </tr>
              </tbody>
          </table>
        </div>
        <div className="col-3"/>
      </div>
    </main>
  );
}

export default App;
